// 单个电影的组件
import singleMovie from '../movie-tpl/single-movie.vue'
// 请求封装
import reqeust from '../../../util/request.js'
export default {
	name: "home",
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			cardCur: 0,
			noticeShow: false,
			swiperList: [],
			dotStyle: false,
			towerStart: 0,
			direction: '',
			movieList: [],
			hotList: []
		}
	},
	onLoad() {
		this.TowerSwiper('swiperList');
		// 初始化towerSwiper 传已有的数组名即可
		// 获取【大家都在看的电影】

	},
	created: function() {
		this.getMovicLimit()
		this.getHotList()
		this.getBannerList()
	},
	methods: {
		DotStyle(e) {
			this.dotStyle = e.detail.value
		},
		// cardSwiper
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		// towerSwiper
		// 初始化towerSwiper
		TowerSwiper(name) {
			let list = this[name];
			for (let i = 0; i < list.length; i++) {
				list[i].zIndex = parseInt(list.length / 2) + 1 - Math.abs(i - parseInt(list.length / 2))
				list[i].mLeft = i - parseInt(list.length / 2)
			}
			this.swiperList = list
		},

		// towerSwiper触摸开始
		TowerStart(e) {
			this.towerStart = e.touches[0].pageX
		},

		// towerSwiper计算方向
		TowerMove(e) {
			this.direction = e.touches[0].pageX - this.towerStart > 0 ? 'right' : 'left'
		},

		// towerSwiper计算滚动
		TowerEnd(e) {
			let direction = this.direction;
			let list = this.swiperList;
			if (direction == 'right') {
				let mLeft = list[0].mLeft;
				let zIndex = list[0].zIndex;
				for (let i = 1; i < this.swiperList.length; i++) {
					this.swiperList[i - 1].mLeft = this.swiperList[i].mLeft
					this.swiperList[i - 1].zIndex = this.swiperList[i].zIndex
				}
				this.swiperList[list.length - 1].mLeft = mLeft;
				this.swiperList[list.length - 1].zIndex = zIndex;
			} else {
				let mLeft = list[list.length - 1].mLeft;
				let zIndex = list[list.length - 1].zIndex;
				for (let i = this.swiperList.length - 1; i > 0; i--) {
					this.swiperList[i].mLeft = this.swiperList[i - 1].mLeft
					this.swiperList[i].zIndex = this.swiperList[i - 1].zIndex
				}
				this.swiperList[0].mLeft = mLeft;
				this.swiperList[0].zIndex = zIndex;
			}
			this.direction = ""
			this.swiperList = this.swiperList
		},
		getMore: function(type) {
			uni.navigateTo({
				url: '/pages/component/movie-tpl/more-movie?type=' + type
			});
		},
		getMovicLimit: function() {
			let _that = this;
			reqeust.get(reqeust.url.GET_MOVIC_LIMIT, null).then(res => {
				_that.movieList = res;
			})
		},
		getHotList: function() {
			let _that = this;
			reqeust.get(reqeust.url.HOT_List, null).then(res => {
				_that.hotList = res;
			})
		},
		hideModal: function() {
			this.noticeShow = false
		},
		getBannerList: function() {
			let _that = this;
			reqeust.get(reqeust.url.BANNER, null).then(res => {
				_that.swiperList = res;
			})
		},
		jumpPage: function(e) {
			let typeId = e.currentTarget.dataset.type
			if (typeId === 0) {
				uni.navigateTo({
				    url: e.currentTarget.dataset.jump
				});
			}
		}
	},
	components: {
		singleMovie
	}

}
